/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #000;
	font-family: sans-serif;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.4;
}

@media screen and (min-width: 700px) {
	body,
	button,
	input,
	select,
	optgroup,
	textarea {
		font-size: 18px;
		font-size: 1.125rem;
	}
}

h1, h2, h3, h4, h5, h6 {
	margin-top: .5em;
	margin-bottom: .17em;
	font-weight: 700;
	font-family: 'Source Sans Pro',"Lucida Grande","Lucida Sans Unicode","Lucida Sans",Geneva,Arial,sans-serif;
}

@media screen and (max-width: 575px) {
	h1 {
		font-size: 2.1em;
	}
	h2 {
		font-size: 1.8em;
	}
	h3 {
		font-size: 1.6em;
	}
	h4 {
		font-size: 1.3em;
	}
	h5 {
		font-size: 1.1em;
	}
	h6 {
		font-size: .9em;
	}
}

p {
	mmargin-top: .4em;
	margin-bottom: .8em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	position: relative;
	margin: 0 0 1em;
	background: #eee;
	padding: 1em 2em .5em 5em;
	font-style: italic;
}

blockquote:before {
	position: absolute;
	top: -.2em;
	left: -.13em;
	content: "“";
	font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-size: 12em;
	color: white;
	font-weight: 400;
